const initialState = {
    formData: [],
    errors: []
}

const FormDataReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'formData':
            return {
                ...action.payload
            }; 
        case 'errorsData':
            return {
                ...state,errors: action.obj.errors
            }; 
        default:
            return state;
    }
};

export default FormDataReducer;